
%btn {
    color: #fff;
    text-decoration: none;
    font-weight: 400;
    font-size: rems(20px);
    text-transform: uppercase;
    display: inline-block;
    border-radius: 5px;
    padding: 14px 32px;
    margin: 0 8px 16px 0;
}

@mixin button($color) {
    @extend %btn;
    background-color: $color;
    &:hover {
        background-color: darken($color, 8%)
    }
}

.btn {
    @include button($color-button-dark)
}

%btn-small {
    @extend %btn;
    font-size: rems(17px);
    padding: 5px 15px;
    border-radius: 3px;
    line-height: rems(35px);
}